<template>
    <v-container fluid>
        <v-row align="start">
            <v-col sm="3" class="nav">
                <v-card
                    class="mb-2 px-2 py-1 d-flex align-center"
                    :elevation="4"
                >
                    <v-btn
                        small
                        class="mr-4 grey darken-1 white--text"
                        :to="{ name: 'Home' }"
                    >
                        <v-icon> mdi-arrow-left </v-icon>
                    </v-btn>
                    <h3>PT 00003 Information</h3>
                    <v-icon>mdi-information</v-icon>
                </v-card>
                <v-card :elevation="8">
                    <v-list
                        v-for="nav in navs"
                        class="list"
                        :key="nav.name"
                        dense
                    >
                        <v-subheader class="subheader">{{
                            nav.name
                        }}</v-subheader>
                        <v-divider />
                        <v-list-item
                            v-for="item in nav.items"
                            class="list-item"
                            :key="item"
                        >
                            <v-list-item-content>
                                <v-list-item-title>
                                    {{ item }}
                                </v-list-item-title>
                            </v-list-item-content>
                            <v-list-item-action>
                                <v-switch
                                    @change="handleToggleNav(item)"
                                    color="red darken-3"
                                    :value="openNavsName.includes(item)"
                                    hide-details
                                />
                            </v-list-item-action>
                        </v-list-item>
                    </v-list>
                    <v-divider />
                </v-card>
            </v-col>
            <v-col sm="9">
                <div class="items">
                    <div v-for="item in items" :key="item">
                        <v-card class="card" :elevation="8">
                            <div
                                class="d-flex align-center justify-space-between"
                            >
                                <h2 class="title px-2">{{ item }}</h2>
                                <v-btn depressed @click="handleToggleNav(item)">
                                    <v-icon> mdi-close </v-icon>
                                </v-btn>
                            </div>
                            <v-divider />
                            <table-list :table-name="item"/>
                        </v-card>
                    </div>
                </div>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import TableList from '@/components/tables/TableList'
import { TTableNavsAll } from '@/utils/types/tableNavs'
export default {
    name: 'PtInfo',
    components: {
        TableList
    },
    data: () => ({
        openNavsName: [],
        navs: TTableNavsAll
    }),
    computed: {
        items() {
            return this.navs
                .reduce((acc, cur) => acc.concat(cur.items), [])
                .filter(item => this.openNavsName.includes(item))
        }
    },
    methods: {
        isOpen(item) {
            return this.openNavsName.include(item)
        },
        handleToggleNav(item) {
            if (this.openNavsName.includes(item)) {
                this.openNavsName = this.openNavsName.filter(
                    nav => nav !== item
                )
            } else {
                this.openNavsName.push(item)
            }
        }
    }
}
</script>

<style lang="scss" scoped>
