    position: sticky;
    top: 0;
}

.list {
    padding: 0;
}

.items {
    column-count: 3;
    column-gap: 40px;

    & > div {
        display: inline-block;
        width: 100%;
        margin-bottom: 20px;
    }
}

.list-item {
    min-height: 30px;
    height: 30px;
}

.card {
    position: unset;
}

.subheader {
    height: 30px;
}
</style>
